// general
.tails {
  background-color: #fff;
  a:link{text-decoration:underline; color:#000;}
  a:visited{text-decoration:underline; color:#000;}
  a:hover{text-decoration:underline; color:#000;}
  a:active{text-decoration:underline; color:#000;}
  a.web:visited{text-decoration:underline; color:#000;}
  h1{
    font-weight: bold;
  }
  h2{
    font-weight: normal;
  }
}
.tails {
  position: absolute;
  z-index: 3;
  top: 100%;

  overflow-y: scroll;

  width: 100%;
  height: 100%;

  will-change: transform;
}

.trigger__info {
  position: absolute;
  z-index: 10;

  width: 100%;
  padding: 10px 0;

  text-align: center;
}

.trigger__info--arrow {
  bottom: 0;
  left: 50%;

  width: 26px;
  height: 20px;
  margin-left: -13px;

  opacity: .5;
  color: #fff;
}

// sections
.tails__section {
  position: relative;

  max-width: 600px;
  margin: 0 auto;
  padding: 50px;

  *zoom: 1;
  &:after {
    display: table;
    content: ' ';
    clear: both;
  }
  will-change: transform;
}

.tails__section:last-child {
  padding-bottom: 200px;
}

.tails__link {
  font-size: 24px;

  display: block;

  margin: 40px 0;

  color: #121212;
}

.tails__section__el {
  position: relative;
  opacity: 0;
}

.tails__column {
  float: left;

  width: 50%;
}

.tails__column--left {
  float: left;
}

.tails__column--right {
  float: right;
}

.tails__column__container--left {
  padding-right: 50px;
}

.tails__column__container--right {
  padding-left: 50px;
}

@media screen and (max-width: 600px) {
  .tails__column {
    float: none;

    width: 100%;
  }

  .tails__column__container {
    padding: 0;
  }
}
.tails__section__el .bold{
  font-weight: bold;
}
